@import 'base';

.container {
  height: 100%;
  /* momentum scrolling */
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}

.children {
  height: calc(100% - 48px);
  overflow-y: scroll;
  background-color: $background;
}



